import './App.css';
import axios from 'axios';
import { useEffect, useState, useTransition } from 'react';
import { Table, Form, InputGroup } from 'react-bootstrap';
import { useQuery } from '@tanstack/react-query';
import 'bootstrap/dist/css/bootstrap.min.css';

const Hangul = require('hangul-js');

function App() {

  let [coin, setCoin] = useState([])
  let [coinPrice, setCoinPrice] = useState([])


  /* GET COIN DATA */
  useEffect(() => {
    axios
      .request({
        method: 'GET',
        url: 'https://api.upbit.com/v1/market/all?isDetails=false',
        headers: { accept: 'application/json' }
      })
      .then(function (response) {
        let array = response.data;
        /* COIN KRW SORTING */
        let market = array.map((row, idx) => { if (row.market.indexOf('KRW') !== -1) { return row } });
        market = market.filter(function (item) { return item !== null && item !== undefined && item !== ''; });
        setCoin(market);
      })
      .catch(function (error) {
        console.error(error);
      });
  }, [])


  /* COIN PARAM */
  let market_param = coin.map((row, idx) => { return row.market });
  let coinalpha = market_param.map((row, idx) => { return row.substring(4, row.length) })
  let market_img = []

  for (var i = 0; i < coinalpha.length; i++) {
    market_img[i] = 'https://static.upbit.com/logos/' + coinalpha[i] + '.png'
  }

  market_param = market_param.join(",")

  /* GET COIN PRICE INFO */
  const getCoin = async () => {
    let result = await axios
      .request({
        method: 'GET',
        url: 'https://api.upbit.com/v1/ticker',
        headers: { accept: 'application/json' },
        params: { 'markets': market_param }
      })
      .then(function (response) {
        let data = coin.map((item, i) => Object.assign({}, item, response.data[i]));
        setCoinPrice(data);
        return true;
      })
      .catch(function (error) {
        return false;
      })
    return result;
  };

  useQuery(['coin'], () => getCoin(), { refetchInterval: 500 })

  return (
    <div className="App">
      <p className="subject">가상화폐 실시간 시세</p>

      <Table bordered hover >
        <thead className="table-head">
          <tr>
            <th>#</th>
            <th>코인명</th>
            <th>현재가</th>
            <th>전일대비</th>
            <th>52주 고가대비</th>
            <th>52주 저가대비</th>
            <th>24시간 거래액</th>
          </tr>
        </thead>
        <tbody>
          {
            coinPrice.map((item, i) => {

              if (item.change === 'EVEN') {
                item.stylecolor = null;
              } else if (item.change === 'FALL') {
                item.stylecolor = { 'color': 'red' };
              } else if (item.change === 'RISE') {
                item.stylecolor = { 'color': 'green' };
              }

              if ((((item.trade_price - item.highest_52_week_price) / item.highest_52_week_price) * 100) > 0) {
                item.stylecolor2 = { 'color': 'green' };
              } else {
                item.stylecolor2 = { 'color': 'red' };
              }

              if ((((item.trade_price - item.lowest_52_week_price) / item.lowest_52_week_price) * 100) > 0) {
                item.stylecolor3 = { 'color': 'green' };
              } else {
                item.stylecolor3 = { 'color': 'red' };
              }

              return (
                <tr>
                  <td><img className="coin-img" src={market_img[i]} /></td>
                  <td>{item.korean_name}</td>
                  <td>\ {item.trade_price.toLocaleString()}</td>
                  <td style={item.stylecolor}>{(item.signed_change_rate * 100).toLocaleString()} %</td>
                  <td style={item.stylecolor2}>{(((item.trade_price - item.highest_52_week_price) / item.highest_52_week_price) * 100).toLocaleString()} %</td>
                  <td style={item.stylecolor3}>{(((item.trade_price - item.lowest_52_week_price) / item.lowest_52_week_price) * 100).toLocaleString()} %</td>
                  <td>\ {item.acc_trade_price_24h.toLocaleString()}</td>
                </tr>
              )
            })}
        </tbody>
      </Table>
    </div >
  );
}

export default App;